import React from "react";
import BotCard from "./BotCard";

function BotCollection({ bots, addBot, deleteBot }) {
  // Your code here
  const botItem = bots.map((bot) => {
    return (
      //Collection of all bots
      <BotCard
        key={bot.id}
        bot={bot}
        clickEvent={addBot}
        deleteBot={deleteBot}
       
      />
    );
  });
  return (
    <div className="container mt-4">
      <div className="row">
      
        {botItem}
      </div>
    </div>
  );
}

export default BotCollection;